@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.13.0/js/all.min.js);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #282c34;
  color: #fff;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.footer {

}

i{opacity:0;font-size:18px;color:#fff;will-change:transform;transform:scale(0.1);transition:all 0.2s ease}h2{pointer-events:none}.cards{display:flex;flex-wrap:wrap}.card{position:relative;width:600px;background-color:#fff;overflow:hidden;margin:10px 10px 10px;height:200px}.card h2{z-index:99;font-family:"Poppins", sans-serif;position:absolute;bottom:0;right:80px;font-size:60px;font-weight:700;color:#fff;margin:10px 0;display:block;width:15ch;overflow:hidden;white-space:nowrap;text-overflow:ellipsis}.card .fa-arrow-right{z-index:100;position:absolute;right:75px;bottom:25px;font-size:40px;cursor:pointer}.card p{z-index:99;position:absolute;top:20px;right:70px;color:#333;opacity:0.7;font-size:12px;letter-spacing:1px;-webkit-writing-mode:vertical-lr;-ms-writing-mode:tb-lr;writing-mode:vertical-lr;transition:all 0.2s ease}.card img{position:absolute;width:50px;right:10px;top:10px;z-index:1}.card .pic{z-index:100;width:calc(100% - 80px);height:200px;background-size:100%;-webkit-filter:grayscale(100%);filter:grayscale(100%);background-position:center;box-shadow:#000 -10px 0 100px inset}.card .social{position:absolute;left:60px;top:0;display:flex;justify-content:space-around;align-items:center;width:180px;height:64px;border-radius:80px}.card .social i:nth-of-type(1){transition-delay:0.4s}.card .social i:nth-of-type(2){transition-delay:0.3s}.card .social i:nth-of-type(3){transition-delay:0.2s}.card .social i:nth-of-type(4){transition-delay:0.1s}.card:hover i{opacity:1;transform:scale(1)}.card button{position:absolute;right:14px;bottom:14px;width:30px;height:30px;background-color:#000;border:none;border-radius:30px;cursor:pointer;outline:none;transition:all 0.3s ease;mix-blend-mode:multiply}.card button i{font-size:3rem}.card:hover button{transform:scale(16.5)}.card:hover p{color:#fff}.card:hover img{-webkit-filter:invert(1);filter:invert(1)}.card:hover .pic{-webkit-filter:grayscale(50%);filter:grayscale(50%)}


